import axios from 'axios';


export const serverapi = async (tablenminfo, data = null) => {


  const crudinfoarr = tablenminfo.split('/');
  const tablenm = crudinfoarr[0]; //
  const pk = crudinfoarr[1] ? crudinfoarr[1] : null; // pk
  const crud = crudinfoarr[2] ? crudinfoarr[2] : null; //
 
  //  /api//insert select curd 
  /*   
     1.  post serverapi(, )
     2.  get serverapi()  
     3.  get serverapi(/id)   
     4.  post serverapi(/id/m, ) 
     5.  post serverapi(/id/d) 
  */
  try {


    if (data) {
      //    data 
           
      axios.post(`/api/${tablenm}`, 
      { headers: { "Content-Type": "application/json", }, 
        body: data })
      .then((response)=>{
        console.log('',response);
        return response;
       })
       .catch((err) =>{
        console.log("   ")
       }); 
        
    }
 
    else {
      //, 
      if(crud === "d"){ 
        //  /id/crud
        return await axios.post(`/api/${crudinfoarr}`,  
        { headers: { "Content-Type": "application/json", } });
      }else{
        // 
        return await axios.get(`/api/${crudinfoarr}`);
      }
      
      
    }

  } catch (error) {
    console.error("  :", error);
  }

}